
/* Ranking Section
--------------------------------*/

#ranking {
  padding: 60px 0;
}

#ranking .item-user{
  background:#fff;
  padding:10px;
  border-radius:6px;
  position:relative;
  box-shadow: -3px 3px 15px -2px rgba(0,0,0,0.52);
  width: 100%;
  height:130px;
}

#ranking .item-user .data{
 margin-top: 35px;
 position:relative;
}

#ranking .item-user .container-img{
  left:20px;
  top:-30px;
}


#ranking .item-user .container-position{
  right:0px;
  top:0px;
  background: #111;
  width: 40px;
  text-align: center;
  border-top-right-radius: 6px;
}

#ranking .item-user .container-position span{
  font-size:25px;
  color:#fff;
}

#ranking .item-user .container-img img{
  width:70px;
  margin-bottom: 10px;
  border-radius:50%;
  box-shadow: -3px 3px 15px -2px rgba(0,0,0,0.52);
}

#ranking .item-user .container-pts {
  background: rgb(61, 53, 53);
    color: #fff;
    position: absolute;
    left: -10px;
    right: -10px;
    bottom: -60px;
    top: 40px;
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding:10px;
}

#ranking .item-user .data .pts{
  color: rgb(235, 43, 43);
  font-size: 18px;
}
  




