

/* Torneos Section
--------------------------------*/

#torneos {
  background: url("./img/img1.jpg") center center no-repeat fixed;
  background-size: cover;
  padding: 60px 0 40px 0;
  position: relative;
}

#torneos::before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.923);
  z-index: 9;
} 

#torneos .container {
  position: relative;
  z-index: 10;
}

#torneos .custome-card{
  box-shadow: -3px 3px 15px -2px rgba(0,0,0,0.52);
}





